@media(min-width: 48em) {
	html {
		font-size: 16px;
	}
	.sidebar {
		bottom: 0;
		left: 0;
		position: fixed;
		text-align: left;
		top: 0;
		width: 18rem;
	}
	.sidebar-sticky {
		left: 1rem;
		position: absolute;
		right: 1rem;
		top: 1rem;
	}
	.content {
		margin-left: 20rem;
		margin-right: 2rem;
		max-width: 38rem;
	}
	.layout-reverse {
		.sidebar {
			left: auto;
			right: 0;
		}
		.content {
			margin-left: 2rem;
			margin-right: 20rem;
		}
	}
}

@media(min-width: 58em) {
	html {
		font-size: 18px;
		font-weight: 400;
		line-height: 1.611em; // golden ratio
	}
}

.sidebar {
	background-color: #300030;
	color: rgb(255, 255, 255);
	color: rgba(255, 255, 255, 0.5);
	padding: 2rem 1rem;
	text-align: center;
	a {
		color: #ffffff;
	}
}

.sidebar-nav {
	list-style: none;
	margin-bottom: 2rem;
	margin-top: 2rem;
	padding-left: 0;
}

.sidebar-nav-item {
	display: block;
	line-height: 1.75;
	.active {
		font-weight: bold;
	}
}

a {
	border-width: 2px;
	text-decoration: none;
	word-wrap: break-word;
	&.sidebar-nav-item {
		&:hover {
			text-decoration: underline;
		}
		&:focus {
			text-decoration: underline;
		}
	}
	&:focus {
		border-bottom-style: solid;
		border-width: 2px;
		text-decoration: none;
		background: transparent;
		background-color: transparent;
		word-wrap: break-word;
		&.tag {
			border-bottom-style: none;
			text-decoration: none;
		}
	}
	&:hover {
		border-bottom-style: solid;
		border-width: 2px;
		text-decoration: none;
		background: transparent;
		background-color: transparent;
		word-wrap: break-word;
		&.tag {
			border-bottom-style: none;
			text-decoration: none;
		}
	}
	&.tag {
		border-bottom-style: none;
		text-decoration: none;
	}
}

.content {
	padding-bottom: 2rem;
	padding-top: 2rem;
}

@media(min-width: 64em) {
	.content {
		margin-left: 20rem;
		margin-right: 2rem;
		max-width: 46.056rem;
	}
	.layout-reverse .content {
		margin-left: 4rem;
		margin-right: 22rem;
	}
}

h1, h2, h3, h4 {
	font-weight: 400;
}

h1 {
	font-size: 2.15rem;
	line-height: 1.25;
}

h2 {
	font-size: 1.85rem;
}

h3 {
	font-size: 1.5rem;
}

h4 {
	font-size: 1.3rem;
}

.img--circle {
	border-radius: 50%;
}

.img--headshot {
	height: 115px;
	width: 115px;
}

.site__title {
	font-size: 3rem;
	margin-bottom: 0;
	a {
		&:hover {
			text-decoration: none;
			border: none;
		}
		&:focus {
			text-decoration: none;
			border: none;
		}
	}
}

.site__description {
	font-size: 1.285rem;
	font-weight: 300;
}

.social {
	a {
		border: none;
		text-decoration: none;
	}
}

.text-center {
	text-align: center;
}

pre {
	border: 1px solid #eee;
	border-radius: 4px;
	padding: 1px;
	tab-size: 2;
	code {
		background-color: #f9f2f4;
		border-radius: 4px;
		padding: 2px 4px;
		white-space: pre-wrap;
	}
}

.hljs {
	overflow-x: auto;
	white-space: pre;
}

code {
	background-color: #f9f2f4;
	border-radius: 4px;
	padding: 2px 4px;
	white-space: pre-wrap;
	font-size: 84%;
}

li code {
	background-color: #f9f2f4;
	border-radius: 4px;
	padding: 2px 4px;
	white-space: pre-wrap;
}

td code {
	background-color: #f9f2f4;
	border-radius: 4px;
	padding: 2px 4px;
	white-space: pre-wrap;
}

.copyright,
.poweredby {
	font-size: .75rem;
}

.element--center {
	display: block;
	margin-left: auto;
	margin-right: auto;
}

.meta {
	font-size: .8rem;
	font-weight: 300;
}

.category {
	font-weight: bold;
}

.tag {
	background: #d6d9e0;
	border-radius: 2px;
	color: #606570;
	font-size: 12px;
	font-weight: bold;
	padding: 2px 6px;
}

.page__title {
	font-size: 2.15rem;
}

.post__subtitle {
	display: block;
	font-size: 1.5rem;
	font-style: italic;
	padding: 0 0 1rem 0;
}

.post-list__item {
	margin-bottom: 3em;
}

.item__title--big {
	display: block;
	font-size: 1.785rem;
	line-height: 1.25;
}

.item__title--small {
	font-size: 1rem;
}

.item__date {
	color: #9a9a9a;
	display: block;
	margin-bottom: .2rem;
	margin-top: .2rem;
}

.page__nav {
	margin-top: 0;
	font-size: .9rem;
}

.comment--heading {
	font-size: 1.5rem;
	text-transform: uppercase;
}

@media(max-width: 768px) {
	.hidden-tablet {
		display: none;
	}
}

.pull-right {
	float: right;
}
